*{
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    color: rgb(59, 59, 59);
    font-size: 1.3rem;
    width: 100%;
}
.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    overflow: hidden;
}
.card p {
    padding: 60px;
    text-align: center;

}

.card img { 
    max-height: 170px;
}